/**
 * Reset Password Modal
 * @author ryan bian
 */
import Alert from 'antd/es/alert';
import Form from 'antd/es/form';
import { WrappedFormUtils } from 'antd/es/form/Form';
import Input from 'antd/es/input';
import Modal from 'antd/es/modal';
import PasswordStrength from 'components/PasswordStrength/';
import { Map } from 'immutable';
import * as React from 'react';

import styles from '../index.module.less';

const FormItem = Form.Item;
const formLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 12 },
  style: {
    display: 'flex',
    alignItems: 'center',
  },
};

interface IResetPasswordProps {
  form?: WrappedFormUtils;
  data: Map<string, any>;
  onSubmit: (data: object) => void;
  closePasswordModal: () => void;
}

@Form.create({})
class ResetPassword extends React.PureComponent<IResetPasswordProps> {
  public componentWillReceiveProps(nextProps) {
    if (nextProps.data.get('show') && !this.props.data.get('show')) {
      nextProps.form.resetFields();
    }
  }
  public handleChangeNewPassword = () => {
    const { isFieldTouched, validateFields } = this.props.form;
    if (isFieldTouched('confirmUserPassword')) {
      setTimeout(() => {
        validateFields(['confirmUserPassword'], {
          force: true,
        });
      }, 0);
    }
  };
  public handleSubmit = () => {
    const {
      onSubmit,
      form: { validateFields },
    } = this.props;
    validateFields((errors, values) => {
      if (!errors) {
        onSubmit(values);
      }
    });
  };
  public handleClose = () => {
    this.props.closePasswordModal();
  };
  public renderForm() {
    const { getFieldDecorator, getFieldValue } = this.props.form;
    return (
      <Form className={styles.ResetPassword__Form}>
        <FormItem {...formLayout} required label="">
          {getFieldDecorator('userPassword', {
            rules: [
              { required: true, message: '' },
              { whitespace: true, message: '' },
            ],
          })(<Input type="password" placeholder="" />)}
        </FormItem>
        <FormItem {...formLayout} required label="">
          {getFieldDecorator('newUserPassword', {
            rules: [
              { required: true, message: '' },
              { min: 6, message: '6' },
              { max: 18, message: '18' },
              { whitespace: true, message: '' },
              {
                pattern: /^[a-zA-Z0-9,./\\@!]+$/,
                message: '',
              },
              {
                validator(rule, value, callback) {
                  const errors = [];
                  if (value === getFieldValue('userPassword')) {
                    errors.push(new Error(''));
                  }
                  callback(errors);
                },
              },
            ],
          })(
            <Input
              type="password"
              placeholder=""
              onChange={this.handleChangeNewPassword}
            />,
          )}
        </FormItem>
        <FormItem {...formLayout} label="">
          <PasswordStrength
            value={getFieldValue('newUserPassword') || ''}
            style={{ width: '70%' }}
          />
        </FormItem>
        <FormItem {...formLayout} required label="">
          {getFieldDecorator('confirmUserPassword', {
            rules: [
              { required: true, message: '' },
              {
                validator(rule, value, callback) {
                  const errors = [];
                  if (value !== getFieldValue('newUserPassword')) {
                    errors.push(new Error(''));
                  }
                  callback(errors);
                },
              },
            ],
          })(<Input type="password" placeholder="" />)}
        </FormItem>
      </Form>
    );
  }
  public render() {
    const { data } = this.props;
    const modalProps = {
      title: '',
      visible: data.get('show'),
      width: 600,
      okText: '',
      onOk: this.handleSubmit,
      onCancel: this.handleClose,
      confirmLoading: data.get('pending'),
      maskClosable: false,
      centered: true,
    };
    return (
      <Modal {...modalProps}>
        <Alert
          message=""
          type="warning"
          showIcon
        />
        {this.renderForm()}
      </Modal>
    );
  }
}

export default ResetPassword;
