import qs from 'qs';
import request, { mocker } from 'utils/request';
// import { USE_MOCK } from '../../CONFIG';
const USE_MOCK = false;

// API list
const API_LIST =
  '/ehuodiGateway/utmsDispatch/utmsDrivercs/selectDriverListByCondition';
const API_DELETE = '/ehuodiGateway/utmsDispatch/utmsDrivercs/deleteUtmsDriver'; // 
const API_ADDITION = '/ehuodiGateway/utmsDispatch/utmsDrivercs/addUtmsDriver'; // 
const API_UPDATE = '/ehuodiGateway/utmsDispatch/utmsDrivercs/modifyUtmsDriver'; // 
const API_BINDING =
  '/ehuodiGateway/utmsDispatch/utmsDrivercs/bindingUtmsDriverAndVehicle'; // 
const API_DETAILS =
  '/ehuodiGateway/utmsDispatch/utmsDrivercs/selectUtmsDriverDetailByPhone'; // 
const API_DRIVER =
  '/ehuodiGateway/utmsDispatch/utmsVehiclecs/selectUnbindingUtmsVehicleList'; // 
const API_DELETEVEHICLE =
  '/ehuodiGateway/utmsDispatch/utmsDrivercs/untiedUtmsDriverAndVehicle'; // 
const API_VEHICLEDETAILS =
  '/ehuodiGateway/utmsDispatch/utmsVehiclecs/selectUtmsVehicleDetail'; // 

if (process.env.NODE_ENV === 'development') {
  // 
  //  tree shaking 
  mocker
    .on('post', API_LIST)
    .reply(() => import('./mock/list').then(exports => [200, exports.default]));
}

/**
 * 
 */
export const getList = ops =>
  request(
    {
      method: 'post',
      url: API_LIST,
      data: qs.stringify(ops),
    },
    {
      useMock: USE_MOCK,
      globalErrorMsg: true,
    },
  ).then(res => res);

