import qs from 'qs';
import request, { mocker } from 'utils/request';
import { USE_MOCK } from '../../CONFIG';

// API list
const API_GETDETAIL = '/ehuodiGateway/utmsCore/utmsCustomercs/selectCustomer';

if (process.env.NODE_ENV === 'development') {
  // 
  //  tree shaking 
  mocker
    .on('post', API_GETDETAIL)
    .reply(() => import('./mock/mock').then(exports => [200, exports.default]));
}

/**
 * 
 * 
 */
export const getData = data => {
  return request(
    {
      method: 'post',
      url: API_GETDETAIL,
      data: qs.stringify(data),
    },
    {
      useMock: USE_MOCK,
      globalErrorMsg: true,
    },
  );
};
