/**
 * fixpath.list Service
 * @author ryan bian
 */
import qs from 'qs';
import request, { mocker } from 'utils/request';
import { USE_MOCK } from '../../CONFIG';

// API list
const PATHLIST_RESOURCE = '/ehuodiGateway/utmsDispatch/routecs/queryList';

if (process.env.NODE_ENV === 'development') {
  // 
  //  tree shaking 
  mocker
    .on('post', PATHLIST_RESOURCE)
    .reply(() => import('./mock/list').then(exports => [200, exports.default]));
}

/**
 * 
 * 
 */
export const getResources = data => {
  return request(
    {
      method: 'post',
      url: PATHLIST_RESOURCE,
      data: qs.stringify(data),
    },
    {
      useMock: false,
      globalErrorMsg: true,
    },
  );
};
